module.exports = "\n\nbody {\n  font-family: 'montserrat', sans-serif;\n  /* background-image: url('bg.jpg'); */\n  background-size: cover;\n  background-position: top center;\n}\n\n.app-wrap {\n  display: flex;\n  flex-direction: column;\n  min-height: 100vh;\n  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6));\n}\n\nheader {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 50px 15px 15px;\n}\n\nheader input {\n  width: 100%;\n  max-width: 280px;\n  padding: 10px 15px;\n  border: none;\n  outline: none;\n  background-color: rgba(255, 255, 255, 0.3);\n  border-radius: 16px 0px 16px 0px;\n  border-bottom: 3px solid #DF8E00;\n  \n  color: #313131;\n  font-size: 20px;\n  font-weight: 300;\n  transition: 0.2s ease-out;\n}\n\nheader input:focus {\n  background-color: rgba(255, 255, 255, 0.6);\n}\n\nmain {\n  flex: 1 1 100%;\n  padding: 25px 25px 50px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n}\n\n.location .city {\n  color: #FFF;\n  font-size: 32px;\n  font-weight: 500;\n  margin-bottom: 5px;\n}\n\n.location .date {\n  color: #FFF;\n  font-size: 16px;\n}\n\n.current .temp {\n  color: #FFF;\n  font-size: 102px;\n  font-weight: 900;\n  margin: 30px 0px;\n  text-shadow: 2px 10px rgba(0, 0, 0, 0.6);\n}\n\n.current .temp span {\n  font-weight: 500;\n}\n\n.current .weather {\n  color: #FFF;\n  font-size: 32px;\n  font-weight: 700;\n  font-style: italic;\n  margin-bottom: 15px;\n  text-shadow: 0px 3px rgba(0, 0, 0, 0.4);\n}\n\n.current .hi-low {\n  color: #FFF;\n  font-size: 24px;\n  font-weight: 500;\n  text-shadow: 0px 4px rgba(0, 0, 0, 0.4);\n}\n\n#forecast {\n    color: #222;\n    display: flex;\n    flex-direction: row;\n    flex-wrap: nowrap;\n    font-family: 'Helvetica','Arial',sans-serif;\n    margin: 40px auto;\n    width: 100%;\n}\n\n#forecast p {\n    margin: 0;\n}\n\n#forecast .card {\n    flex-basis: 0;\n    flex-grow: 1;\n    margin: 0 5px;\n}\n\n#forecast .card-body {\n    background: #fff;\n    border-radius: 3px;\n    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);\n    padding: 10px;\n    text-align: center;\n}\n\n#forecast .card .title {\n    font-size: 14px;\n    font-weight: bold;\n}\n\n#forecast .card .icon {\n    margin: 10px 0 5px;\n    width: 60px;\n}\n\n#forecast .card .wx {\n    font-size: 12px;\n}\n\n#forecast .card .temps {\n    font-size: 16px;\n    font-weight: bold;\n    margin-top: 20px;\n}\n\n#forecast .card .temps span {\n    color: #666;\n    font-size: 12px;\n    font-weight: normal;\n    padding-right: 6px;\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"